"use server";

import { auth, currentUser } from "@clerk/nextjs/server";
import { FREE_LIMIT, getUsage, incrementUsage as incrementUsageDb } from "@/lib/usage";
import { getUserSubscriptionStatus } from "@/lib/subscription";
import { stripe } from "@/lib/stripe";
import { redirect } from "next/navigation";

// 1. CHECK USAGE STATUS
export async function checkUsage() {
  const { userId } = auth();

  // If not logged in, return locked state
  if (!userId) {
    return { usage: 0, remaining: 0, limit: FREE_LIMIT, isLocked: true, isPremium: false };
  }

  // ðŸ”¥ ADMIN OVERRIDE â€” UNLOCK EVERYTHING FOR YOUR ACCOUNT
  const user = await currentUser();
  const email = user?.emailAddresses?.[0]?.emailAddress || "";

  if (email === "solobotagency@gmail.com") {
    return {
      usage: 0,
      remaining: 9999,
      limit: FREE_LIMIT,
      isLocked: false,
      isPremium: true,
      plan: "ADMIN",
    };
  }

  // Check Subscription Status
  const subscription = await getUserSubscriptionStatus(userId);

  // PREMIUM / ADMIN LOGIC -> UNLIMITED ACCESS
  if (subscription.isPremium) {
    return {
      usage: 0,
      remaining: 9999,
      limit: FREE_LIMIT,
      isLocked: false,
      isPremium: true,
      plan: subscription.plan,
    };
  }

  // FREE TIER LOGIC
  const usage = await getUsage(userId);
  const currentCount = usage.count || 0;

  const remaining = Math.max(0, FREE_LIMIT - currentCount);
  const isLocked = currentCount >= FREE_LIMIT;

  return {
    usage: currentCount,
    remaining,
    limit: FREE_LIMIT,
    isLocked,
    isPremium: false,
  };
}

// 2. INCREMENT USAGE
export async function incrementUsage() {
  const { userId } = auth();
  if (!userId) return;

  const subscription = await getUserSubscriptionStatus(userId);

  // Premium users do NOT increment usage count
  if (subscription.isPremium) return;

  await incrementUsageDb(userId);
}

// 3. TRACK USAGE (Alias)
export async function trackUsage() {
  await incrementUsage();
}

// 4. CREATE CHECKOUT SESSION
export async function createCheckoutSession() {
  const { userId } = auth();
  if (!userId) return;

  const priceId = process.env.STRIPE_PRO_MONTHLY_PRICE_ID;
  const appUrl = process.env.NEXT_PUBLIC_APP_URL || "http://localhost:3000";

  if (!priceId) {
    console.error("âŒ Missing STRIPE_PRO_MONTHLY_PRICE_ID");
    return;
  }

  try {
    const session = await stripe.checkout.sessions.create({
      mode: "subscription",
      payment_method_types: ["card"],
      line_items: [
        {
          price: priceId,
          quantity: 1,
        },
      ],
      metadata: {
        userId: userId,
      },
      success_url: `${appUrl}/dashboard/ai-sales-agent?success=true`,
      cancel_url: `${appUrl}/dashboard/ai-sales-agent?canceled=true`,
    });

    if (session.url) {
      redirect(session.url);
    }
  } catch (error) {
    console.error("Stripe session creation failed", error);
    redirect("/dashboard/billing");
  }
}
