type TemplateKey = keyof typeof salesTemplates;

// --- Types ---

type TabMode = "email" | "variations" | "linkedin" | "icebreakers" | "analysis" | "rewrite";

interface AnalysisResult {
  spamScore?: number;
  tone?: {
    perceived?: string;
    formality?: number;
    aggression?: number;
    humanLikeness?: number;
  };
  readability?: {
    gradeLevel?: string;
    complexity?: number;
    sentences?: number;
    avgWords?: number;
  };
  replyProbability?: number;
  ctaStrength?: number;
  suggestions?: string[];
}

// --- Utilities ---

// âœ… CRITICAL FIX: Universal formatter to flatten ANY object shape from API
function formatOutput(item: any): string {
  if (item === null || item === undefined) return "";
  if (typeof item === "string") return item;
  
  if (Array.isArray(item)) {
    return item.map(formatOutput).join("\n\n");
  }

  if (typeof item === "object") {
    // 1. Handle structured email objects (The cause of your crash)
    if (item.introduction || item.pain_points || item.offer || item.call_to_action) {
        return [
            item.headline, 
            item.introduction,
            item.pain_points,
            item.offer,
            item.call_to_action,
            item.ps 
        ].filter(Boolean).map(s => String(s)).join("\n\n");
    }

    // 2. Direct keys commonly used by OpenAI models
    if (item.rewritten) return formatOutput(item.rewritten);
    if (item.script) return formatOutput(item.script);
    if (item.text) return formatOutput(item.text);
    if (item.message) return formatOutput(item.message);
    if (item.content) return formatOutput(item.content);
    if (item.body) return formatOutput(item.body);
    if (item.subject) return formatOutput(item.subject);

    // 3. Headline/Body structure
    if (item.headline && item.body) {
      return `${item.headline}\n\n${item.body}`;
    }

    // 4. LinkedIn Object to String Conversion
    if (item.connectionNotes || item.followUpDMs || item.voiceNoteScripts) {
      let liText = "";
      const processSection = (key: string, title: string) => {
        if (item[key] && Array.isArray(item[key])) {
          liText += `### ${title}\n${item[key].map((n: any) => `â€¢ ${formatOutput(n)}`).join('\n')}\n\n`;
        }
      };
      processSection("connectionNotes", "Connection Notes");
      processSection("followUpDMs", "DM Follow-Ups");
      processSection("voiceNoteScripts", "Voice Note Scripts");
      return liText.trim();
    }

    // 5. Fallback: Check if it's a wrapper object
    const keys = Object.keys(item);
    if (keys.length > 0) {
       return keys.map(k => {
         const val = item[k];
         return typeof val === 'string' ? val : formatOutput(val);
       }).join("\n\n");
    }

    // 6. Last resort: JSON stringify cleanly so it ALWAYS returns a string
    try {
      return JSON.stringify(item, null, 2).replace(/["{}]/g, "");
    } catch (e) {
      return "";
    }
  }
  
  return String(item);
}

// Standard Button Style Constant
const CTA_BUTTON_CLASS = "px-6 py-2.5 bg-[#00C26D] hover:bg-[#00A05A] disabled:opacity-50 text-white font-bold rounded-lg flex items-center justify-center gap-2 transition-all shadow-lg shadow-green-900/20 text-sm";

export default function SalesAgentPage() {
  // --- Global State ---
  const [activeTab, setActiveTab] = useState<TabMode>("email");
  const [isLoading, setIsLoading] = useState(false);
  const [copiedId, setCopiedId] = useState<string | null>(null);
  const [showAdvanced, setShowAdvanced] = useState(false);

  // --- Usage State Variables ---
  const [usage, setUsage] = useState(0);
  const [limit, setLimit] = useState(3);
  const [remaining, setRemaining] = useState(3);
  const [isLocked, setIsLocked] = useState(false);
  const [isPremium, setIsPremium] = useState(false);

  // --- Campaign Context ---
  const [activeTemplate, setActiveTemplate] = useState("");
  const [formData, setFormData] = useState({
    companyName: "",
    targetAudience: "",
    productDetails: "",
    painPoints: "",
    tone: "Persuasive",
    length: "Medium",
    structure: "Auto",
    style: "Direct",
    ctaType: "Soft",
    creativity: 0.7,
    includeCaseStudy: false,
    includeObjections: false,
    includeRiskReversal: false,
  });

  // --- Tool Specific Outputs ---
  const [emailOutput, setEmailOutput] = useState("");
  const [variationsOutput, setVariationsOutput] = useState<any[]>([]);
  const [abOutput, setAbOutput] = useState<any[]>([]);
  const [subjectsOutput, setSubjectsOutput] = useState<any[]>([]);
  const [linkedinOutput, setLinkedinOutput] = useState<string>(""); 
  const [icebreakersOutput, setIcebreakersOutput] = useState<any[]>([]);
  
  // Analysis & Rewrite
  const [analysisInput, setAnalysisInput] = useState("");
  const [analysisResult, setAnalysisResult] = useState<AnalysisResult | null>(null);
  const [rewriteInput, setRewriteInput] = useState("");
  const [rewriteRule, setRewriteRule] = useState("More Persuasive");
  const [rewriteOutput, setRewriteOutput] = useState("");

  // --- Initialize Usage ---
  useEffect(() => {
    async function loadUsage() {
      const data = await checkUsage();
      setUsage(data.usage);
      setLimit(data.limit);
      setRemaining(data.remaining);
      setIsLocked(data.isLocked);
      setIsPremium(data.isPremium);
    }
    loadUsage();
  }, []);

  // --- Actions ---

  const copyToClipboard = (text: string, id: string) => {
    if (!text) return;
    navigator.clipboard.writeText(text);
    setCopiedId(id);
    setTimeout(() => setCopiedId(null), 2000);
  };

  const handleTemplateChange = (key: string) => {
    setActiveTemplate(key);
    // @ts-ignore
    const t = salesTemplates[key as TemplateKey];
    if (t) {
      setFormData((prev) => ({
        ...prev,
        targetAudience: t.targetAudience || "",
        productDetails: t.productDetails || "",
        painPoints: t.painPoints || "",
        tone: t.tone || "Persuasive",
        length: t.length || "Medium",
      }));
    }
  };

  // --- API Functions ---

  const generateEmail = async () => {
    if (isLocked) {
      // Allow UI to show lock screen instead of alert
      return;
    }

    setIsLoading(true);
    try {
      // 1. Track Usage
      await trackUsage();

      const res = await fetch("/api/bots/ai-sales-agent", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(formData),
      });
      const data = await res.json();
      if (data.result) {
        const cleanText = formatOutput(data.result);
        setEmailOutput(cleanText);
        setAnalysisInput(cleanText);
        setRewriteInput(cleanText);

        // 2. Refresh Usage
        const info = await checkUsage();
        setUsage(info.usage);
        setRemaining(info.remaining);
        setIsLocked(info.isLocked);
      }
    } catch (e) {
      console.error(e);
    } finally {
      setIsLoading(false);
    }
  };

  const generateVariations = async (type: "variations" | "ab-test" | "subjects") => {
    if (isLocked) return;
    setIsLoading(true);
    
    if (type === "variations") setVariationsOutput([]);
    if (type === "ab-test") setAbOutput([]);
    if (type === "subjects") setSubjectsOutput([]);

    try {
      await trackUsage();

      const res = await fetch("/api/bots/ai-sales-agent/variations", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ formData, type, count: type === "variations" ? 3 : 2 }),
      });
      const data = await res.json();
      
      const rawResult = data.result || [];
      const results = Array.isArray(rawResult) ? rawResult : [rawResult];
      
      if (type === "variations") {
        setVariationsOutput(results.map(r => formatOutput(r)));
      } 
      else if (type === "ab-test") {
        let abResults = [];
        if (results.length === 1 && typeof results[0] === 'object' && !results[0].label && !results[0].text) {
           const obj = results[0];
           abResults = Object.keys(obj).map(k => ({ 
             label: k, 
             text: formatOutput(obj[k]) 
           }));
        } else {
           abResults = results.map((r: any, i: number) => ({
             label: r.label || `Variant ${i+1}`,
             text: formatOutput(r.text || r)
           }));
        }
        setAbOutput(abResults);
      } 
      else if (type === "subjects") {
        const normalizedSubjects = results.map((s: any) => {
           if (typeof s === 'string') return { subject: s, openRateScore: 85, spamRisk: 'Low' };
           const subText = s.subject || s.headline || s.text || formatOutput(s);
           return {
             subject: subText,
             openRateScore: s.openRateScore || 85,
             spamRisk: s.spamRisk || 'Low'
           };
        });
        setSubjectsOutput(normalizedSubjects);
      }
      
      const info = await checkUsage();
      setUsage(info.usage);
      setIsLocked(info.isLocked);

    } catch (e) {
      console.error(e);
    } finally {
      setIsLoading(false);
    }
  };

  const generateLinkedin = async () => {
    if (isLocked) return;
    setIsLoading(true);
    try {
      await trackUsage();

      const res = await fetch("/api/bots/ai-sales-agent/linkedin", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(formData),
      });
      const data = await res.json();
      if (data.result) setLinkedinOutput(formatOutput(data.result));

      const info = await checkUsage();
      setUsage(info.usage);
      setIsLocked(info.isLocked);

    } catch (e) {
      console.error(e);
    } finally {
      setIsLoading(false);
    }
  };

  const generateIcebreakers = async () => {
    if (isLocked) return;
    setIsLoading(true);
    setIcebreakersOutput([]);
    try {
      await trackUsage();
      
      const res = await fetch("/api/bots/ai-sales-agent/icebreakers", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ targetAudience: formData.targetAudience, companyName: formData.companyName }),
      });
      const data = await res.json();
      const results = Array.isArray(data.result) ? data.result : [data.result];
      setIcebreakersOutput(results.map(r => formatOutput(r)).filter(s => s.length > 0));

      const info = await checkUsage();
      setUsage(info.usage);
      setIsLocked(info.isLocked);

    } catch (e) {
      console.error(e);
    } finally {
      setIsLoading(false);
    }
  };

  const runAnalysis = async () => {
    if (isLocked) return;
    if (!analysisInput) return;
    setIsLoading(true);
    try {
      await trackUsage();

      const res = await fetch("/api/bots/ai-sales-agent/analysis", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ text: analysisInput, targetAudience: formData.targetAudience }),
      });
      const data = await res.json();
      
      const r = data.result || {};
      
      const safeResult: AnalysisResult = {
        spamScore: r.spamScore ?? 0,
        replyProbability: r.replyProbability ?? 50,
        ctaStrength: r.ctaStrength ?? 5,
        tone: {
          perceived: r.tone?.perceived || "Neutral",
          formality: r.tone?.formality ?? 5,
          aggression: r.tone?.aggression ?? 5,
          humanLikeness: r.tone?.humanLikeness ?? 5
        },
        readability: {
          gradeLevel: r.readability?.gradeLevel || "Grade 8",
          complexity: r.readability?.complexity ?? 5,
          sentences: r.readability?.sentences ?? 0,
          avgWords: r.readability?.avgWords ?? 0
        },
        suggestions: Array.isArray(r.suggestions) ? r.suggestions : ["Review for clarity.", "Check spam triggers."]
      };
      
      setAnalysisResult(safeResult);

      const info = await checkUsage();
      setUsage(info.usage);
      setIsLocked(info.isLocked);

    } catch (e) {
      console.error(e);
    } finally {
      setIsLoading(false);
    }
  };

  const runRewrite = async () => {
    if (isLocked) return;
    if (!rewriteInput) return;
    setIsLoading(true);
    setRewriteOutput("");
    try {
      await trackUsage();

      const res = await fetch("/api/bots/ai-sales-agent/rewrite", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ original: rewriteInput, instruction: rewriteRule }),
      });
      const data = await res.json();
      if (data.result) setRewriteOutput(formatOutput(data.result));

      const info = await checkUsage();
      setUsage(info.usage);
      setIsLocked(info.isLocked);

    } catch (e) {
      console.error(e);
    } finally {
      setIsLoading(false);
    }
  };

  // --- Render Helpers ---

  const renderLinkedinSections = (text: string) => {
    if (!text) return <EmptyState message="Generate LinkedIn assets to see them here." />;
    
    const sections = text.split('###').filter(s => s.trim().length > 0);
    
    if (sections.length === 0 && text.trim().length > 0) {
        return (
            <div className="space-y-3">
               <OutputCard text={formatOutput(text)} id="li-raw" onCopy={copyToClipboard} copiedId={copiedId} />
            </div>
        );
    }

    return sections.map((section, idx) => {
      const [titleLine, ...lines] = section.trim().split('\n');
      const content = lines.join('\n').trim();
      const items = content.split('â€¢').filter(i => i.trim().length > 0);
      const displayItems = items.length > 0 ? items : [content];

      return (
        <div key={idx} className="space-y-3 mb-6">
          <h4 className="text-sm font-bold text-slate-400 uppercase flex items-center gap-2">
            <Linkedin className="w-4 h-4 text-[#00C26D]" /> {titleLine.replace(/:/g, '')}
          </h4>
          <div className="space-y-3">
            {displayItems.map((item, i) => (
              <OutputCard key={i} text={formatOutput(item).trim()} id={`li-${idx}-${i}`} onCopy={copyToClipboard} copiedId={copiedId} />
            ))}
          </div>
        </div>
      );
    });
  };

  return (
    <div className="min-h-screen bg-[#0B1120] text-slate-200 font-sans relative">
      
      {/* --- LOCK SCREEN OVERLAY --- */}
      {isLocked && (
        <div className="absolute inset-0 flex flex-col items-center justify-center bg-black/70 backdrop-blur-md text-white text-center p-8 rounded-2xl z-50 fixed top-0 left-0 w-full h-full">
            <h2 className="text-2xl font-bold mb-4">Free Limit Reached</h2>
            <p className="text-white/70 mb-6">Youâ€™ve used all your free requests for the Sales Agent.</p>

            <button
            onClick={createCheckoutSession}
            className="px-6 py-3 bg-[#00C26D] hover:bg-[#00A05A] text-white font-bold rounded-xl shadow-lg shadow-green-900/40 transition-all transform hover:scale-105"
            >
            Upgrade to Unlock Unlimited Access
            </button>
        </div>
      )}

      {/* Header */}
      <div className="bg-[#1E293B] border-b border-slate-700/50 px-6 py-6 sticky top-0 z-30 shadow-md">
        <div className="max-w-7xl mx-auto flex items-center justify-between">
          <div className="flex items-center gap-4">
            <div className="p-2.5 rounded-xl bg-[#0F172A] border border-slate-700 shadow-lg shadow-green-900/10">
                <Bot className="w-6 h-6 text-[#00C26D]" />
            </div>
            <div>
                <h1 className="text-2xl font-bold text-white tracking-tight">AI Sales Agent</h1>
                <p className="text-slate-400 text-sm">Enterprise-grade outbound campaign engine.</p>
            </div>
          </div>
          {/* UPGRADE BUTTON IN HEADER */}
          {!isPremium && (
             <button
                onClick={createCheckoutSession}
                className="px-3 py-1.5 bg-[#00C26D] hover:bg-[#00A05A] text-white text-xs font-bold rounded-lg shadow-md transition-colors"
             >
                Upgrade Plan
             </button>
          )}
        </div>
      </div>

      <div className="max-w-7xl mx-auto p-4 md:p-6 grid grid-cols-1 lg:grid-cols-12 gap-6">
        
        {/* --- LEFT SIDEBAR: CAMPAIGN CONTEXT --- */}
        <div className="lg:col-span-4 space-y-6">
          <div className="bg-[#1E293B] border border-slate-700/50 rounded-2xl p-5 shadow-xl h-fit overflow-y-auto max-h-[calc(100vh-140px)] custom-scrollbar">
            <div className="flex items-center gap-2 mb-6 border-b border-slate-700/50 pb-4">
              <Briefcase className="w-5 h-5 text-[#00C26D]" />
              <h2 className="text-lg font-bold text-white">Campaign Setup</h2>
            </div>

            {/* Template Selector */}
            <div className="mb-5">
              <label className="text-xs font-bold text-slate-500 uppercase tracking-wider mb-2 flex justify-between">
                Framework <span className="text-[#00C26D] cursor-pointer hover:underline" onClick={() => setActiveTemplate("")}>Reset</span>
              </label>
              <select
                value={activeTemplate}
                onChange={(e) => handleTemplateChange(e.target.value)}
                className="w-full bg-[#0F172A] border border-slate-600 text-xs text-white rounded-lg px-3 py-2.5 focus:border-[#00C26D] outline-none cursor-pointer"
              >
                <option value="">Select a template...</option>
                {Object.entries(salesTemplates).map(([key, t]) => (
                  <option key={key} value={key}>{t.label}</option>
                ))}
              </select>
            </div>

            {/* Inputs */}
            <div className="space-y-4">
              <InputGroup label="Target Audience" value={formData.targetAudience} onChange={(v) => setFormData({ ...formData, targetAudience: v })} icon={Target} placeholder="e.g. CTOs at Series B SaaS" />
              <InputGroup label="Company Name" value={formData.companyName} onChange={(v) => setFormData({ ...formData, companyName: v })} icon={Briefcase} placeholder="e.g. SoloBot" />

              <div>
                <label className="block text-xs font-bold text-slate-500 uppercase mb-2">Value Proposition</label>
                <textarea
                  value={formData.productDetails}
                  onChange={(e) => setFormData({ ...formData, productDetails: e.target.value })}
                  className="w-full bg-[#0F172A] border border-slate-700 rounded-lg p-3 text-sm text-white h-24 resize-none focus:border-[#00C26D] outline-none placeholder-slate-600 focus:ring-1 focus:ring-[#00C26D]"
                  placeholder="What is the core offer?"
                />
              </div>

              <div>
                <label className="block text-xs font-bold text-slate-500 uppercase mb-2">Pain Points</label>
                <textarea
                  value={formData.painPoints}
                  onChange={(e) => setFormData({ ...formData, painPoints: e.target.value })}
                  className="w-full bg-[#0F172A] border border-slate-700 rounded-lg p-3 text-sm text-white h-20 resize-none focus:border-[#00C26D] outline-none placeholder-slate-600 focus:ring-1 focus:ring-[#00C26D]"
                  placeholder="What keeps them up at night?"
                />
              </div>

              <div className="grid grid-cols-2 gap-3">
                <SelectGroup label="Tone" value={formData.tone} onChange={(v) => setFormData({ ...formData, tone: v })} options={["Persuasive", "Casual", "Professional", "Urgent", "Empathetic", "Direct", "Witty"]} />
                <SelectGroup label="Length" value={formData.length} onChange={(v) => setFormData({ ...formData, length: v })} options={["Short", "Medium", "Long"]} />
              </div>

              {/* Advanced Toggle */}
              <div className="pt-2">
                <button 
                  onClick={() => setShowAdvanced(!showAdvanced)} 
                  className="flex items-center justify-between w-full text-xs font-bold text-slate-400 hover:text-white uppercase tracking-wider py-2 border-t border-slate-700/50 mt-2"
                >
                  <div className="flex items-center gap-2"><Sliders className="w-3 h-3" /> Advanced Strategy</div>
                  {showAdvanced ? <ChevronDown className="w-3 h-3 rotate-180 transition-transform" /> : <ChevronDown className="w-3 h-3 transition-transform" />}
                </button>

                {showAdvanced && (
                  <div className="space-y-4 mt-3 animate-in slide-in-from-top-2 duration-200 bg-[#0F172A]/50 p-3 rounded-xl border border-slate-700/50">
                    <div className="grid grid-cols-2 gap-3">
                      <SelectGroup label="Structure" value={formData.structure} onChange={(v) => setFormData({...formData, structure: v})} options={["Auto", "PAS", "HVA", "4S"]} />
                      <SelectGroup label="Style" value={formData.style} onChange={(v) => setFormData({...formData, style: v})} options={["Direct", "Narrative", "Authority"]} />
                    </div>
                    <SelectGroup label="CTA Type" value={formData.ctaType} onChange={(v) => setFormData({...formData, ctaType: v})} options={["Soft", "Neutral", "Hard"]} />
                    
                    <div className="space-y-2">
                      <div className="flex justify-between text-xs text-slate-400">
                        <span>Creativity</span>
                        <span>{formData.creativity}</span>
                      </div>
                      <input 
                        type="range" min="0" max="1" step="0.1" 
                        value={formData.creativity} 
                        onChange={(e) => setFormData({...formData, creativity: parseFloat(e.target.value)})}
                        className="w-full h-1 bg-slate-700 rounded-lg appearance-none cursor-pointer accent-[#00C26D]"
                      />
                    </div>

                    <div className="space-y-2 pt-2">
                      <Toggle label="Include Case Study" checked={formData.includeCaseStudy} onChange={(v) => setFormData({...formData, includeCaseStudy: v})} />
                      <Toggle label="Pre-empt Objections" checked={formData.includeObjections} onChange={(v) => setFormData({...formData, includeObjections: v})} />
                      <Toggle label="Risk Reversal" checked={formData.includeRiskReversal} onChange={(v) => setFormData({...formData, includeRiskReversal: v})} />
                    </div>
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>

        {/* --- RIGHT SIDEBAR: TOOLS --- */}
        <div className="lg:col-span-8">
          <div className="bg-[#1E293B] border border-slate-700/50 rounded-2xl shadow-xl overflow-hidden min-h-[800px] flex flex-col">
            
            {/* Toolbar */}
            <div className="flex border-b border-slate-700/50 bg-[#162032] overflow-x-auto no-scrollbar">
              <TabButton id="email" label="Email Generator" icon={MessageSquare} active={activeTab === 'email'} onClick={setActiveTab} />
              <TabButton id="variations" label="Variations" icon={LayoutGrid} active={activeTab === 'variations'} onClick={setActiveTab} />
              <TabButton id="linkedin" label="LinkedIn" icon={Linkedin} active={activeTab === 'linkedin'} onClick={setActiveTab} />
              <TabButton id="icebreakers" label="Icebreakers" icon={Zap} active={activeTab === 'icebreakers'} onClick={setActiveTab} />
              <TabButton id="analysis" label="Analysis" icon={BarChart3} active={activeTab === 'analysis'} onClick={setActiveTab} />
              <TabButton id="rewrite" label="Rewrite Studio" icon={PenTool} active={activeTab === 'rewrite'} onClick={setActiveTab} />
            </div>

            {/* Content Area */}
            <div className="p-6 flex-1 bg-[#1E293B] relative flex flex-col">
              
              {/* === TAB 1: EMAIL GENERATOR === */}
              {activeTab === "email" && (
                <div className="h-full flex flex-col animate-in fade-in duration-300">
                  <div className="flex justify-between items-center mb-6">
                    <div>
                      <h3 className="text-lg font-semibold text-white">Cold Email Generator</h3>
                      <p className="text-sm text-slate-400">Main campaign driver.</p>
                    </div>
                    <div className="flex gap-2">
                      {emailOutput && (
                        <button 
                          onClick={generateEmail}
                          disabled={isLoading}
                          className="px-4 py-2 bg-slate-800 hover:bg-slate-700 text-slate-300 font-medium rounded-lg text-sm border border-slate-700 transition-all flex items-center gap-2"
                        >
                          <RotateCcw className="w-3 h-3" /> Regenerate
                        </button>
                      )}
                      <button
                        onClick={generateEmail}
                        disabled={isLoading}
                        className={CTA_BUTTON_CLASS}
                      >
                        {isLoading ? <Loader2 className="w-4 h-4 animate-spin" /> : <Sparkles className="w-4 h-4" />}
                        {emailOutput ? "New Draft" : "Generate"}
                      </button>
                    </div>
                  </div>

                  <div className="flex-1 bg-[#0F172A] border border-slate-700 rounded-xl relative group min-h-[400px] flex flex-col shadow-inner overflow-hidden">
                      {emailOutput ? (
                        <div className="flex flex-col h-full">
                           <div className="bg-gradient-to-r from-slate-800 to-slate-900/50 px-6 py-3 border-b border-slate-700 flex justify-between items-center">
                              <div className="flex gap-2">
                                <div className="w-3 h-3 rounded-full bg-red-500/50"></div>
                                <div className="w-3 h-3 rounded-full bg-yellow-500/50"></div>
                                <div className="w-3 h-3 rounded-full bg-green-500/50"></div>
                              </div>
                              <button
                                onClick={() => copyToClipboard(emailOutput, "email-main")}
                                className="flex items-center gap-1.5 text-xs font-medium text-slate-400 hover:text-white transition-colors"
                              >
                                {copiedId === "email-main" ? <Check className="w-3 h-3 text-[#00C26D]" /> : <Copy className="w-3 h-3" />}
                                {copiedId === "email-main" ? "Copied" : "Copy Text"}
                              </button>
                           </div>
                           <div className="p-8 prose prose-invert max-w-none text-slate-200 text-base leading-relaxed whitespace-pre-wrap font-medium">
                              {emailOutput}
                           </div>
                        </div>
                      ) : (
                        <EmptyState message="Configure your campaign on the left to generate your first draft." />
                      )}
                  </div>
                </div>
              )}

              {/* === TAB 2: VARIATIONS === */}
              {activeTab === "variations" && (
                <div className="space-y-8 animate-in fade-in duration-300">
                  <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                      <ActionButton label="Generate 3 Variations" icon={RefreshCw} onClick={() => generateVariations("variations")} disabled={isLoading} />
                      <ActionButton label="Generate Subject Lines" icon={FileText} onClick={() => generateVariations("subjects")} disabled={isLoading} />
                      <ActionButton label="Run A/B Test" icon={SplitSquareHorizontal} onClick={() => generateVariations("ab-test")} disabled={isLoading} />
                  </div>

                  {isLoading && (
                      <div className="py-20 flex flex-col items-center gap-3">
                        <Loader2 className="w-10 h-10 text-[#00C26D] animate-spin" />
                        <span className="text-slate-500 text-sm animate-pulse">Consulting the AI engine...</span>
                      </div>
                  )}

                  {!isLoading && (
                      <div className="space-y-8">
                        {/* Variations Grid */}
                        {variationsOutput.length > 0 && (
                           <div className="space-y-4">
                              <SectionHeader title="Sales Variations" icon={List} />
                              <div className="grid grid-cols-1 gap-5">
                                 {variationsOutput.map((item, i) => (
                                    <div key={i} className="relative">
                                       <span className="absolute -top-3 left-4 px-2 bg-[#1E293B] text-xs text-[#00C26D] font-bold z-10">Variation {i + 1}</span>
                                       <OutputCard text={formatOutput(item)} id={`var-${i}`} onCopy={copyToClipboard} copiedId={copiedId} />
                                    </div>
                                 ))}
                              </div>
                           </div>
                        )}

                        {/* A/B Test */}
                        {abOutput.length > 0 && (
                           <div className="space-y-4">
                              <SectionHeader title="A/B Split Test" icon={SplitSquareHorizontal} />
                              <div className="grid grid-cols-1 md:grid-cols-2 gap-5">
                                 {abOutput.map((item, i) => (
                                    <div key={i} className="bg-[#0F172A] border border-slate-700 rounded-xl p-6 relative flex flex-col h-full shadow-sm hover:border-[#00C26D]/50 transition-all">
                                       <div className="flex justify-between items-start mb-4">
                                          <span className="px-2 py-1 bg-[#00C26D]/10 text-[#00C26D] text-xs font-bold rounded uppercase tracking-wider">{item.label || `Variant ${i+1}`}</span>
                                          <button
                                            onClick={() => copyToClipboard(formatOutput(item.text || item), `ab-${i}`)}
                                            className="text-slate-500 hover:text-white"
                                          >
                                            {copiedId === `ab-${i}` ? <Check className="w-4 h-4 text-[#00C26D]" /> : <Copy className="w-4 h-4" />}
                                          </button>
                                       </div>
                                       <p className="text-sm text-slate-300 whitespace-pre-wrap leading-relaxed flex-grow">{formatOutput(item.text || item)}</p>
                                    </div>
                                 ))}
                              </div>
                           </div>
                        )}

                        {/* Subjects */}
                        {subjectsOutput.length > 0 && (
                           <div className="space-y-4">
                              <SectionHeader title="Subject Line Generator" icon={FileText} />
                              <div className="bg-[#0F172A] border border-slate-700 rounded-xl p-6 grid gap-2">
                                {subjectsOutput.map((sub, i) => {
                                  const text = typeof sub === 'string' ? sub : (sub.subject || formatOutput(sub));
                                  const score = (typeof sub === 'object' && sub.openRateScore) ? sub.openRateScore : 85;
                                  
                                  return (
                                    <div key={i} className="bg-slate-800/30 border border-slate-700/50 rounded-lg p-3 flex justify-between items-center group hover:border-[#00C26D]/50 transition-all">
                                      <div className="flex items-center gap-3 overflow-hidden">
                                        <div className={`w-8 h-8 rounded-full flex-shrink-0 flex items-center justify-center text-xs font-bold border ${score >= 80 ? 'bg-green-500/10 border-green-500/30 text-green-400' : 'bg-slate-800 border-slate-600 text-slate-400'}`}>
                                          {score}
                                        </div>
                                        <p className="text-sm text-slate-200 font-medium truncate">{text}</p>
                                      </div>
                                      <button onClick={() => copyToClipboard(text, `sub-${i}`)} className="text-slate-500 hover:text-white p-1">
                                        {copiedId === `sub-${i}` ? <Check className="w-4 h-4 text-[#00C26D]" /> : <Copy className="w-4 h-4" />}
                                      </button>
                                    </div>
                                  );
                                })}
                              </div>
                           </div>
                        )}
                        
                        {variationsOutput.length === 0 && abOutput.length === 0 && subjectsOutput.length === 0 && <EmptyState message="Select an option above to generate content." />}
                      </div>
                  )}
                </div>
              )}

              {/* === TAB 3: LINKEDIN === */}
              {activeTab === "linkedin" && (
                <div className="h-full flex flex-col animate-in fade-in duration-300">
                  <div className="flex justify-between items-center mb-6">
                    <div>
                      <h3 className="text-lg font-semibold text-white">LinkedIn Outreach</h3>
                      <p className="text-sm text-slate-400">Social selling assets.</p>
                    </div>
                    <button
                      onClick={generateLinkedin}
                      disabled={isLoading}
                      className={CTA_BUTTON_CLASS}
                    >
                      {isLoading ? <Loader2 className="w-4 h-4 animate-spin" /> : <Linkedin className="w-4 h-4" />}
                      Generate
                    </button>
                  </div>
                  
                  {linkedinOutput ? (
                    <div className="space-y-6 pb-10">
                      {renderLinkedinSections(linkedinOutput)}
                    </div>
                  ) : (
                    <EmptyState message="Generate connection requests, DMs, and voice scripts." />
                  )}
                </div>
              )}

              {/* === TAB 4: ICEBREAKERS === */}
              {activeTab === "icebreakers" && (
                <div className="h-full flex flex-col animate-in fade-in duration-300">
                  <div className="flex justify-between items-center mb-6">
                    <div>
                      <h3 className="text-lg font-semibold text-white">Pattern Interrupt Icebreakers</h3>
                      <p className="text-sm text-slate-400">First lines that get opens.</p>
                    </div>
                    <button
                      onClick={generateIcebreakers}
                      disabled={isLoading}
                      className={CTA_BUTTON_CLASS}
                    >
                      {isLoading ? <Loader2 className="w-4 h-4 animate-spin" /> : <Zap className="w-4 h-4" />}
                      Generate
                    </button>
                  </div>

                  {icebreakersOutput.length > 0 ? (
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-4 pb-4">
                      {icebreakersOutput.map((line, i) => (
                        <OutputCard 
                          key={i} 
                          text={formatOutput(line)} 
                          id={`ice-${i}`} 
                          onCopy={copyToClipboard} 
                          copiedId={copiedId} 
                          style="quote"
                        />
                      ))}
                    </div>
                  ) : (
                    <EmptyState message="Generate 15+ personalized opening lines." />
                  )}
                </div>
              )}

              {/* === TAB 5: ANALYSIS === */}
              {activeTab === "analysis" && (
                <div className="grid grid-cols-1 lg:grid-cols-12 gap-6 h-full animate-in fade-in duration-300">
                  <div className="lg:col-span-5 flex flex-col gap-4 h-full">
                      <div className="flex justify-between items-center">
                        <label className="text-xs font-bold text-slate-500 uppercase">Input Email</label>
                        <button
                          onClick={runAnalysis}
                          disabled={isLoading || !analysisInput}
                          className={CTA_BUTTON_CLASS}
                        >
                          {isLoading ? <Loader2 className="w-3 h-3 animate-spin" /> : <BarChart3 className="w-3 h-3" />}
                          Analyze
                        </button>
                      </div>
                      <textarea
                        value={analysisInput}
                        onChange={(e) => setAnalysisInput(e.target.value)}
                        className="flex-1 bg-[#0F172A] border border-slate-700 rounded-xl p-4 text-sm text-slate-200 focus:border-[#00C26D] outline-none resize-none min-h-[300px]"
                        placeholder="Paste your email here to analyze..."
                      />
                  </div>

                  <div className="lg:col-span-7 bg-[#0F172A] border border-slate-700 rounded-xl p-6 overflow-y-auto h-full shadow-inner">
                      {analysisResult ? (
                        <div className="space-y-8">
                           {/* Scores */}
                           <div>
                              <h4 className="text-xs font-bold text-slate-500 uppercase mb-4">Impact Scores</h4>
                              <div className="space-y-4">
                                <ProgressBar label="Reply Likelihood" value={analysisResult.replyProbability || 0} color="bg-[#00C26D]" />
                                <ProgressBar label="Spam Risk" value={(analysisResult.spamScore || 0) * 10} color={(analysisResult.spamScore || 0) > 5 ? "bg-red-500" : "bg-blue-500"} inverse />
                                <ProgressBar label="CTA Strength" value={(analysisResult.ctaStrength || 0) * 10} color="bg-orange-500" />
                              </div>
                           </div>
                           
                           <div className="grid grid-cols-2 gap-4">
                              <div className="p-4 bg-slate-800/50 rounded-xl border border-slate-700/50">
                                <h5 className="text-xs font-bold text-slate-400 uppercase mb-2">Tone Profile</h5>
                                <div className="text-lg font-bold text-white mb-1">{analysisResult.tone?.perceived || "Neutral"}</div>
                                <div className="text-xs text-slate-500">Human Likeness: {analysisResult.tone?.humanLikeness ?? 0}/10</div>
                              </div>
                              <div className="p-4 bg-slate-800/50 rounded-xl border border-slate-700/50">
                                <h5 className="text-xs font-bold text-slate-400 uppercase mb-2">Readability</h5>
                                <div className="text-lg font-bold text-white mb-1">{analysisResult.readability?.gradeLevel || "N/A"}</div>
                                <div className="text-xs text-slate-500">Sentences: {analysisResult.readability?.sentences ?? 0}</div>
                              </div>
                           </div>

                           <div className="space-y-4">
                              <h4 className="text-xs font-bold text-slate-500 uppercase mb-2">Tone Metrics</h4>
                              <div className="grid grid-cols-3 gap-2 text-center">
                                {/* SAFE NULL CHECKS */}
                                <MetricBox label="Formality" value={analysisResult.tone?.formality ?? 0} />
                                <MetricBox label="Aggression" value={analysisResult.tone?.aggression ?? 0} />
                                <MetricBox label="Human" value={analysisResult.tone?.humanLikeness ?? 0} />
                              </div>
                           </div>

                           <div>
                              <h4 className="text-xs font-bold text-slate-500 uppercase mb-3">AI Suggestions</h4>
                              <div className="space-y-2">
                                {(analysisResult.suggestions || []).map((s, i) => (
                                   <div key={i} className="text-xs text-slate-300 flex gap-3 items-start bg-slate-800/30 p-3 rounded-lg border border-slate-700/30">
                                      <span className="text-yellow-500 mt-0.5"><Wand2 className="w-3 h-3" /></span> 
                                      <span className="leading-relaxed">{s}</span>
                                   </div>
                                ))}
                              </div>
                           </div>
                        </div>
                      ) : (
                        <EmptyState message="Analysis results will appear here." />
                      )}
                  </div>
                </div>
              )}

              {/* === TAB 6: REWRITE STUDIO === */}
              {activeTab === "rewrite" && (
                <div className="grid grid-cols-1 lg:grid-cols-2 gap-6 h-full animate-in fade-in duration-300">
                  <div className="flex flex-col gap-4">
                      <label className="text-xs font-bold text-slate-500 uppercase">Original Text</label>
                      <textarea
                        value={rewriteInput}
                        onChange={(e) => setRewriteInput(e.target.value)}
                        className="flex-1 bg-[#0F172A] border border-slate-700 rounded-xl p-4 text-sm text-slate-200 focus:border-[#00C26D] outline-none resize-none min-h-[300px]"
                        placeholder="Paste text to rewrite..."
                      />
                      <div className="flex gap-2 bg-[#0F172A] p-2 rounded-xl border border-slate-700 items-center">
                        <select
                           value={rewriteRule}
                           onChange={(e) => setRewriteRule(e.target.value)}
                           className="flex-1 bg-transparent text-sm text-white px-2 outline-none cursor-pointer"
                        >
                           {["More Persuasive", "Make Shorter", "Make Longer", "More Casual", "More Professional", "Add Urgency", "Add Emojis", "Remove Emojis"].map(r => (
                              <option key={r} value={r} className="bg-[#1E293B]">{r}</option>
                           ))}
                        </select>
                        <button
                          onClick={runRewrite}
                          disabled={isLoading || !rewriteInput}
                          className={CTA_BUTTON_CLASS}
                        >
                          {isLoading ? <Loader2 className="w-3 h-3 animate-spin" /> : <RefreshCw className="w-3 h-3" />}
                          Rewrite
                        </button>
                      </div>
                  </div>

                  <div className="flex flex-col gap-4">
                      <div className="flex justify-between items-center">
                        <label className="text-xs font-bold text-slate-500 uppercase">Rewritten Result</label>
                        {rewriteOutput && (
                           <button onClick={() => copyToClipboard(rewriteOutput, "rewrite")} className="text-[#00C26D] text-xs flex items-center gap-1 hover:underline font-bold">
                              {copiedId === "rewrite" ? <Check className="w-3 h-3"/> : <Copy className="w-3 h-3"/>} Copy
                           </button>
                        )}
                      </div>
                      <div className="flex-1 bg-[#0F172A] border border-slate-700 rounded-xl p-6 text-sm text-slate-200 whitespace-pre-wrap leading-relaxed relative shadow-inner">
                        {rewriteOutput || (
                          <div className="h-full flex flex-col items-center justify-center text-slate-600 gap-2">
                            <PenTool className="w-8 h-8 opacity-20" />
                            <span className="italic">Result will appear here...</span>
                          </div>
                        )}
                      </div>
                  </div>
                </div>
              )}

            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

// --- Sub-Components ---

const InputGroup = ({ label, value, onChange, icon: Icon, placeholder }: any) => (
  <div>
    <label className="block text-xs font-bold text-slate-500 uppercase mb-2">{label}</label>
    <div className="relative group">
      <input
        type="text"
        value={value}
        onChange={(e) => onChange(e.target.value)}
        className="w-full bg-[#0F172A] border border-slate-700 rounded-lg py-2.5 pl-9 pr-3 text-sm text-white focus:border-[#00C26D] outline-none placeholder-slate-600 transition-all group-hover:border-slate-700"
        placeholder={placeholder}
      />
      <Icon className="absolute left-3 top-2.5 w-4 h-4 text-slate-500 group-hover:text-slate-400 transition-colors" />
    </div>
  </div>
);

const SelectGroup = ({ label, value, onChange, options }: any) => (
  <div>
    <label className="block text-xs font-bold text-slate-500 uppercase mb-2">{label}</label>
    <div className="relative">
      <select
        value={value}
        onChange={(e) => onChange(e.target.value)}
        className="w-full bg-[#0F172A] border border-slate-700 text-xs text-white rounded-lg px-3 py-2.5 focus:border-[#00C26D] outline-none appearance-none cursor-pointer hover:border-slate-700 transition-all"
      >
        {options.map((o: string) => <option key={o} value={o}>{o}</option>)}
      </select>
      <ChevronDown className="absolute right-3 top-3 w-3 h-3 text-slate-500 pointer-events-none" />
    </div>
  </div>
);

const Toggle = ({ label, checked, onChange }: any) => (
  <div onClick={() => onChange(!checked)} className="flex items-center justify-between cursor-pointer group py-1">
    <span className="text-xs text-slate-400 group-hover:text-slate-200 transition-colors">{label}</span>
    <div className={`w-8 h-4 rounded-full relative transition-colors ${checked ? 'bg-[#00C26D]' : 'bg-slate-700'}`}>
      <div className={`absolute top-0.5 w-3 h-3 bg-white rounded-full transition-all shadow-sm ${checked ? 'left-4.5' : 'left-0.5'}`} style={{ left: checked ? '18px' : '2px' }}></div>
    </div>
  </div>
);

const ActionButton = ({ label, icon: Icon, onClick, disabled }: any) => (
  <button
    onClick={onClick}
    disabled={disabled}
    className="flex items-center justify-center gap-2 p-4 bg-[#0F172A] border border-slate-700 hover:border-[#00C26D] rounded-xl text-slate-300 hover:text-white transition-all font-medium disabled:opacity-50 shadow-sm"
  >
    <Icon className="w-5 h-5" />
    {label}
  </button>
);

const OutputCard = ({ text, id, onCopy, copiedId, style }: any) => (
  <div className={`bg-[#0F172A] border border-slate-700 rounded-xl p-5 relative group hover:border-[#00C26D] transition-all shadow-sm ${style === 'quote' ? 'flex flex-col justify-between h-full' : ''}`}>
    <button
      onClick={() => onCopy(text, id)}
      className="absolute top-3 right-3 p-1.5 bg-slate-800 hover:bg-slate-700 rounded-lg text-slate-400 hover:text-white transition-all opacity-0 group-hover:opacity-100"
    >
      {copiedId === id ? <Check className="w-3 h-3 text-[#00C26D]" /> : <Copy className="w-3 h-3" />}
    </button>
    <p className={`text-sm text-slate-300 whitespace-pre-wrap leading-relaxed ${style === 'quote' ? 'italic mb-4' : ''}`}>{text}</p>
    {style === 'quote' && (
      <div className="flex justify-end pt-2 border-t border-slate-700/50 mt-auto">
         <span className="text-[10px] text-slate-500 uppercase tracking-widest">Icebreaker</span>
      </div>
    )}
  </div>
);

const EmptyState = ({ message }: { message: string }) => (
  <div className="h-full flex flex-col items-center justify-center text-slate-500 gap-4 opacity-60 min-h-[200px]">
    <Bot className="w-12 h-12 stroke-1 opacity-50" />
    <p className="text-sm">{message}</p>
  </div>
);

const SectionHeader = ({ title, icon: Icon }: any) => (
  <div className="flex items-center gap-2 pb-2 border-b border-slate-700/50">
    <Icon className="w-4 h-4 text-[#00C26D]" />
    <h4 className="text-sm font-bold text-white">{title}</h4>
  </div>
);

const ProgressBar = ({ label, value, color, inverse }: any) => (
  <div>
    <div className="flex justify-between text-xs mb-1.5">
      <span className="text-slate-400 font-medium">{label}</span>
      <span className="text-white font-bold">{Math.round(value)}%</span>
    </div>
    <div className="h-2 bg-slate-800 rounded-full overflow-hidden border border-slate-700/50">
      <div 
        className={`h-full rounded-full transition-all duration-1000 ease-out ${color}`} 
        style={{ width: `${Math.max(5, Math.min(100, value))}%` }} 
      ></div>
    </div>
  </div>
);

const MetricBox = ({ label, value }: any) => (
   <div className="bg-slate-800/30 p-2 rounded border border-slate-700">
      <div className="text-xs text-slate-500 mb-1">{label}</div>
      <div className="font-bold text-white">{value}/10</div>
   </div>
);

const TabButton = ({ id, label, icon: Icon, active, onClick }: any) => (
  <button
    onClick={() => onClick(id)}
    className={`flex items-center gap-2 px-5 py-4 text-sm font-medium transition-all border-b-2 whitespace-nowrap ${
      active
        ? "border-[#00C26D] text-white bg-[#0F172A]"
        : "border-transparent text-slate-400 hover:text-slate-200 hover:bg-[#0F172A]/50"
    }`}
  >
    <Icon className={`w-4 h-4 ${active ? 'text-[#00C26D]' : 'text-slate-500'}`} />
    {label}
  </button>
);